export default {
	data() {
		return {
			nickName: ''
		};
	},
	onLoad(e) {
		this.nickName=e.name;
	},
	methods: {
		//修改用户昵称
		saveName() {
			let pages = getCurrentPages();  //获取所有页面栈实例列表
			let prevPage = pages[ pages.length - 2 ];  //上一页页面实例
			prevPage.$vm.userInfo.nickname=this.nickName;
			uni.navigateBack({  //uni.navigateTo跳转的返回，默认1为返回上一级
				delta: 1
			});
		}
	}
};
